.sd-action {
  appearance: none;
  border: none;
  background: transparent;
  padding: calcSize(1) calcSize(3);
  color: $primary;
  border-radius: calcSize(12.5);
  font-weight: 600;
  font-family: $font-family;
  font-style: normal;
  font-size: calcSize(2);
  line-height: calcSize(3);
  outline: none;
}
.sd-action--negative {
  color: $red;
}
.sd-action--icon {
  padding: calcSize(1);
}
.sd-action--icon {
  use {
    fill: $foreground-light;
  }
}
svg.sd-action--icon {
  fill: $foreground-light;
}
.sd-action:disabled,
.sd-action--disabled {
  color: $foreground;
  cursor: default;
  opacity: 0.25;
  cursor: default;
  pointer-events: none;
}

.sd-action:hover,
.sd-action:focus {
  outline: none;
  background-color: $primary-light;
  cursor: pointer;
  opacity: 1;
  &.sd-action--icon {
    background-color: $background-dim;
  }
  &.sd-action--negative {
    background-color: $red-light;
    &.sd-action--icon {
      use {
        fill: $red;
      }
    }
  }
  &:active {
    opacity: 0.5;
  }
}
.sd-action__icon {
  display: block;
  width: calcSize(3);
  height: calcSize(3);
}
.sd-action--pressed {
  background-color: $background-dim;
  opacity: 0.5;
}
